import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { NgbTooltipModule } from '@ng-bootstrap/ng-bootstrap';
import { NgSelectModule } from '@ng-select/ng-select';
import { TranslateModule } from '@ngx-translate/core';
import { InlineSVGModule } from 'ng-inline-svg-2';
import { WidgetsModule } from 'src/app/theme/partials';
import { ManageSupplierPaymentComponent } from './manage-supplier-payment/manage-supplier-payment.component';
import { CreateSupplierPaymentComponent } from './create-supplier-payment/create-supplier-payment.component';
import { UpdateSupplierPaymentComponent } from './update-supplier-payment/update-supplier-payment.component';

@NgModule({
  declarations: [
    ManageSupplierPaymentComponent,
    CreateSupplierPaymentComponent,
    UpdateSupplierPaymentComponent,
  ],
  imports: [
    CommonModule,
    NgbTooltipModule,
    FormsModule,
    NgSelectModule,
    InlineSVGModule,
    ReactiveFormsModule,
    TranslateModule,
    RouterModule.forChild([
      {
        path: '',
        component: ManageSupplierPaymentComponent,
      },
      {
        path: 'create',
        component: CreateSupplierPaymentComponent,
      },
      {
        path: 'update/:id/:type',
        component: UpdateSupplierPaymentComponent,
      },
    ]),
    WidgetsModule,
  ],
})
export class SupplierPaymentModule {}
