import { Component } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import * as moment from 'moment';
import { ToastrService } from 'ngx-toastr';
import { SupplierService } from 'src/app/core/services/supplier/supplier.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-update-supplier-payment',
  templateUrl: './update-supplier-payment.component.html',
  styleUrls: ['./update-supplier-payment.component.scss'],
})
export class UpdateSupplierPaymentComponent {
  supplierId = '';
  paymentId = '';
  type = '';
  fetchLoading = false;
  editIndex: any;
  arrAllSuppliers: any = [];
  arrTransferMode: any = [];
  arrBanks: any = [];
  arrPaymentType: any = [
    {
      type: 'G-Voucher (Credit)',
      sentType: 'SERVICE',
      mode: 'Credit',
    },
    {
      type: 'G-Voucher (Debit)',
      sentType: 'SERVICE',
      mode: 'Debit',
    },
    {
      type: 'PAID',
      sentType: 'PAID',
      mode: '',
    },
    {
      type: 'REFUND',
      sentType: 'REFUND',
      mode: 'REFUND',
    },
  ];
  myForm: FormGroup;
  submitted: boolean = false;

  constructor(
    private activatedRoute: ActivatedRoute,
    private supplierService: SupplierService,
    private toastr: ToastrService,
    private formBuilder: FormBuilder,
    private router: Router
  ) {
    activatedRoute.params.subscribe((params) => {
      this.supplierId = params.id;
      this.type = params.type;
    });
    this.myForm = this.formBuilder.group({
      supplierCode: [null, Validators.required],
      supplierName: [null, Validators.required],
      Total_Amount: ['0', Validators.required],
      Refund_Amount: ['0', Validators.required],
      Total_Paid_Amount: ['0', Validators.required],
      Service_Credit_Amount: ['0', Validators.required],
      Service_Debit_Amount: ['0', Validators.required],
      paymentDetails: this.formBuilder.array([]),
    });

    this.getMetaData();
    this.getMetaDataBank();
  }
  getMetaDataBank() {
    const obj = {
      metaType: 'BANK', // Amentities transfermod
    };

    this.supplierService.metaData(obj).subscribe((data) => {
      if (data.success) {
        this.arrBanks = data.data;
        this.getSupplier();
        // this.getAllSupplierAll();
      } else {
        this.arrBanks = [];
      }
    });
  }

  get f() {
    return this.myForm.controls;
  }

  get paymentDetails(): FormArray {
    return this.myForm.get('paymentDetails') as FormArray;
  }
  onPaymentChange(event, i) {
    this.paymentDetails.at(i).patchValue({ mode: event.mode });
  }

  newPaymentDetails(): FormGroup {
    return this.formBuilder.group({
      id: [''],
      type: [null, Validators.required],
      typeView: [null, Validators.required],
      payment_Code: [''],
      mode: [''],
      transferMode: [null, Validators.required],
      transferPercent: [''],
      transferPercentage: [''],
      customerName: [''],
      actualAmount: [''],
      bank: [null],
      verifyDate: [''],
      amount: ['', Validators.required],
      date: ['', Validators.required],
      description: [''],
    });
  }

  removeItem(i: number, item: any) {
    Swal.fire({
      title: 'Do you want to remove?',
      showCancelButton: true,
      confirmButtonText: 'Delete',
      reverseButtons: true,
      confirmButtonColor: '#dc3741',
    }).then((result) => {
      if (result.isConfirmed) {
        const obj = {
          strLoginUserId: localStorage.getItem('userId'),
          fkPaymentId: item.id,
        };
        this.supplierService.deletePaymentSupplier(obj).subscribe((res) => {
          if (res.success) {
            this.paymentDetails.removeAt(i);
          }
        });
      }
    });
  }
  onTransferChange(val, i) {
    this.paymentDetails.at(i).patchValue({ transferPercent: val.percentage });
  }

  addItem() {
    if (
      this.f.supplierCode.value === '' ||
      this.f.supplierCode.value === null
    ) {
      this.toastr.warning('', 'Choose a Supplier');
      return;
    }
    this.paymentDetails.push(this.newPaymentDetails());
  }
  getSupplier() {
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      intSkipCount: 0,
      intPageLimit: 1,
      fkSupplierId: this.supplierId,
      payment_Status: this.type, //or verify
    };

    this.fetchLoading = true;
    this.supplierService.getPaymentSupplier(obj).subscribe((res) => {
      this.fetchLoading = false;
      if (res.success) {
        this.paymentId = res.data[0]._id;
        this.patchValue(res.data[0]);
      } else {
        this.toastr.error('Error While fetching data');
      }
    });
  }

  patchValue(supplier) {
    this.myForm.patchValue({
      supplierCode: supplier.sup_Code,
      supplierName: supplier.supplierName,
      Total_Amount: supplier.Total_Amount,
      Refund_Amount: supplier.Refund_Amount,
      Service_Credit_Amount: supplier.Service_Credit_Amount,
      Service_Debit_Amount: supplier.Service_Debit_Amount,
      Total_Paid_Amount: supplier.Total_Paid_Amount,
    });

    supplier.payment_Details.forEach((item: any, i) => {
      this.addItem();
      this.paymentDetails.at(i).patchValue({
        id: item.payment_id,
        type:
          item.mode === 'Credit'
            ? 'SERVICE'
            : item.mode === 'Debit'
            ? 'SERVICE'
            : item.type === 'PAID'
            ? 'PAID'
            : item.type === 'REFUND'
            ? 'REFUND'
            : '',
        typeView:
          item.mode === 'Credit'
            ? 'G-Voucher (Credit)'
            : item.mode === 'Debit'
            ? 'G-Voucher (Debit)'
            : item.type === 'PAID'
            ? 'PAID'
            : item.type === 'REFUND'
            ? 'REFUND'
            : '',
        payment_Code: item.payment_Code,
        mode:
          item.mode !== '' ? item.mode : item.type === 'REFUND' ? 'REFUND' : '',
        transferMode: item.transferMode,
        transferPercent: item.transferPercent,
        transferPercentage: item.transferPercentage || 0,
        actualAmount: item.actualAmount,
        bank: item.bank,
        customerName: item.customerName,
        verifyDate: item.verifyDate,
        amount: item.amount,
        date: moment(item.date).format('YYYY-MM-DD'),
        description: item.description,
      });
    });
  }

  // getAllSupplierAll() {
  //   const obj = {
  //     strLoginUserId: localStorage.getItem('userId'),
  //     fkSupplierId: this.supplierId,
  //     strName: '',
  //     cust_Code: '',
  //     fromDate: '',
  //     endDate: '',
  //     intSkipCount: '0',
  //     intPageLimit: '1',
  //   };
  //   this.supplierService.getAllSupplier(obj).subscribe((res) => {
  //     if (res.success) {
  //       this.myForm.patchValue({ supplier: res.data[0] });
  //       this.getSupplier();
  //     } else {
  //       this.toastr.error('Error While fetching data');
  //     }
  //   });
  // }

  resetForm() {
    this.myForm.reset();
    this.submitted = false;
  }

  getMetaData() {
    const obj = {
      metaType: 'transfermod', // Amentities transfermod
    };

    this.supplierService.metaData(obj).subscribe((data) => {
      if (data.success) {
        this.arrTransferMode = data.data;
      } else {
        this.arrTransferMode = [];
      }
    });
  }

  editPayment(i) {
    this.editIndex = i;
  }

  updatePayment(i) {
    this.submitted = true;

    console.log('this.myForm', this.myForm);
    if (this.myForm.invalid) {
      return;
    }

    this.myForm.value.paymentDetails.map((paymentDetails) => {
      // if (paymentDetails.transferPercent > 0) {
      //   paymentDetails.amount =
      //     paymentDetails.amount -
      //     paymentDetails.amount * (paymentDetails.transferPercent / 100);
      // }

      return {
        amount: paymentDetails.amount,
        actualAmount: paymentDetails.amount,
        bank: paymentDetails.bank,
        mode: paymentDetails.mode,
        date: paymentDetails.date,
        description: paymentDetails.description,
        transferMode: paymentDetails.transferMode,
        transferPercentage: paymentDetails.transferPercentage,
        type: paymentDetails.type,
      };
    });

    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      fkPaymentId: this.myForm.value.paymentDetails[i].id,
      payment_Details: [this.myForm.value.paymentDetails[i]],
    };

    // console.log('obj', obj);
    this.supplierService.updatePaymentSupplier(obj).subscribe((res) => {
      if (res.success) {
        Swal.fire({
          title: 'Success',
          text: 'Successfully Updated Supplier Payment',
          icon: 'success',
        }).then((value) => {
          if (value) {
            this.myForm.reset();
            this.paymentDetails.removeAt(i);

            this.submitted = false;
            this.editIndex = '';
            this.getSupplier();
            // this.router.navigate(['/supplier-payment']);
          }
        });
      } else {
        Swal.fire({
          title: 'Error',
          text: res.message,
          icon: 'error',
        });
      }
    });
  }
}
